.image-container {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.image-container img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.6);
	display: flex;
	justify-content: center;
	align-items: center;
}

.overlay h2 {
	color: white;
	font-size: 30px;
	text-align: center;
}
